.badge{display:inline-block;width:10px;height:10px;border-radius:30px}.badge--grey{background-color:#C9D1D3}.badge--lime{background-color:#B6E6BD}.badge--purple{background-color:#c781f5}.badge--black{background-color:#110133}.badge--red{background-color:#FF6464}.badge--green{background-color:#42b883}.badge--blue{background-color:#64c4ed}.badge--pink{background-color:#FFBBCC}

.list{margin-bottom:30px}.list__add-button{opacity:.4}.list__add-button:hover{opacity:1}.list li{display:flex;align-items:center;padding:10px 12px;cursor:pointer}.list li:hover .list__remove-icon{opacity:0.2}.list li span{flex:1 1;text-overflow:ellipsis;overflow:hidden;width:160px;white-space:nowrap}.list li:hover{background:rgba(255,255,255,0.6)}.list li .list__remove-icon{opacity:0}.list li .list__remove-icon:hover{opacity:0.8}.list li.active{background:#FFFFFF;box-shadow:0px 3px 10px rgba(0,0,0,0.04);border-radius:4px}.list li .badge{position:relative;left:5px}.list li i{display:inline-flex;margin-right:8px}.list li i img{opacity:.6}

.add-list__popup{width:235px;height:150px;background:#FFFFFF;box-shadow:0px 6px 10px rgba(0,0,0,0.05);border-radius:10px;padding:18px;box-sizing:border-box;position:relative;top:-25px;left:10px}.add-list__popup .button{width:100%;margin-top:15px}.add-list__popup-close-btn{position:absolute;right:-8px;top:-8px;cursor:pointer}.add-list__popup-colors{display:flex;margin-top:12px;justify-content:space-between}.add-list__popup-colors .badge{width:16px;height:16px;cursor:pointer;border:2px solid transparent}.add-list__popup-colors .badge.active{border:2px solid #525252}

.tasks:not(:last-of-type){margin-bottom:40px}.tasks a{text-decoration:none}.tasks__title{font-family:Montserrat;font-weight:bold;font-size:32px;line-height:39px;color:#64C4ED;padding-bottom:20px;border-bottom:1px solid #f2f2f2}.tasks__title:hover img{opacity:.2}.tasks__title img{opacity:0;cursor:pointer;margin-left:15px}.tasks__title img:hover{opacity:.8}.tasks__form{margin-top:20px}.tasks__form-block .button{margin-right:10px;margin-top:15px}.tasks__form-new{transition:.15s ease-in-out;display:flex;text-align:center;cursor:pointer;opacity:0.4}.tasks__form-new:hover{opacity:0.8}.tasks__form-new img{width:16px;height:16px;margin-left:4px}.tasks__form-new span{font-size:14px;margin-left:19px}.tasks__items{margin-top:30px}.tasks__items h2{font-style:normal;font-weight:bold;font-size:22px;color:#C9D1D3;text-align:center}.tasks__items-row{display:flex;align-self:center;margin-bottom:15px}.tasks__items-row:hover .tasks__items-row-actions{opacity:1}.tasks__items-row-actions{display:flex;opacity:0;transition:opacity 0.15s ease-in-out}.tasks__items-row-actions div{display:flex;align-items:center;justify-content:center;width:32px;height:32px;background-color:#fafbfc;border-radius:6px;margin-left:5px;cursor:pointer}.tasks__items-row-actions div:hover{background-color:#eef1f4}.tasks__items-row-actions div:hover svg{opacity:0.9}.tasks__items-row-actions div svg{width:11px;height:11px;opacity:0.4}.tasks__items-row p{margin-left:15px;border:0;font-size:16px;width:100%}.tasks .checkbox{margin-top:-2px}.tasks .checkbox input{display:none}.tasks .checkbox svg{transition:.15s ease-in-out}.tasks .checkbox svg path{stroke:#b2b2b2}.tasks .checkbox input:checked+label{background-color:#4DD599;border-color:#4DD599}.tasks .checkbox input:checked+label svg{opacity:1}.tasks .checkbox input:checked+label svg path{stroke:#fff}.tasks .checkbox:hover label{background-color:#f2f2f2;border-color:#f2f2f2}.tasks .checkbox:hover label svg{opacity:1}.tasks .checkbox:hover label svg path{stroke:#b2b2b2}.tasks .checkbox label{cursor:pointer;display:flex;align-items:center;justify-content:center;border:2px solid #E8E8E8;border-radius:30px;width:20px;height:20px;transition:background-color .15s ease-in-out, border-color .15s ease-in-out}.tasks .checkbox label svg{opacity:0;margin-left:1px}

*{margin:0;padding:0;list-style:none;outline:none;font-family:'Roboto',-apple-system, BlinkMacSystemFont, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.todo{display:flex;position:absolute;left:50%;top:50%;width:calc(100vw - 40px);height:calc(100vh - 100px);transform:translate(-50%, -50%);box-shadow:1px 2px 20px #f3f3f3;border-radius:10px;border:1px solid #f1f1f1;font-size:14px}.todo__sidebar{background-color:#f4f6f8;width:200px;height:calc(100% - 120px);border-right:1px solid #f1f1f1;padding:60px 20px}.todo__tasks{flex:1 1;padding:60px;overflow:auto}.button{background:#4DD599;border-radius:4px;color:#fff;border:0;padding:10px 20px;cursor:pointer;font-size:14px}.button:hover{background:#31ce88}.button:active{background:#2ec180}.button:disabled{background:#d8d8d8}.button--grey{background:#f4f6f8;color:#444444}.button--grey:hover{background:#ebeef2}.button--grey:active{background:#e1e7ec}.field{background:#FFFFFF;border:1px solid #EFEFEF;box-sizing:border-box;border-radius:4px;padding:8px 12px;width:100%;font-size:12px}.field:focus{border-color:#dbdbdb}

